<template>
<div>
  <h1 class="title"></h1>
  <h2 class="subtitle" v-if="loading">...</h2>
  <template v-else>
    <h2 class="subtitle">Error: {{errors.length}}</h2>
    <p class="has-text-danger" v-for="(error, n) in errors" :key="`error_${n}`">{{error}}</p>
  </template>
</div>
</template>

<script>

import { test } from '@/worker'

export default {
  data() {
    return {
      errors: [],
      loading: true
    }
  },
  watch: {
    async diff() {
      this.loading = true
      this.errors = await test(this.issue)
      this.loading = false
    }
  },
  async mounted() {
    this.errors = await test(this.issue)
    this.loading = false
  },
  props: ['diff', 'issue']
}
</script>

